<template>
  <div>
    <el-form
      :model="auditForm"
      status-icon
      :rules="auditFormRule"
      ref="auditForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="auditFlag">
        <el-radio v-model="auditForm.auditFlag" label="4"></el-radio>
        <el-radio v-model="auditForm.auditFlag" label="5"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="auditDesc">
        <el-input type="textarea" :rows="2" placeholder="" v-model="auditForm.auditDesc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="cancle('auditForm')"></el-button>
       <el-button type="primary" @click="onSubmit('auditForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
//   props: {
//     idStr: Number //ID
//   },
  data() {
    return {
      auditForm: {
        auditFlag: '4', // 
        auditDesc: '' // 
      },
      auditFormRule: {
        auditDesc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit(auditForm) {
      this.$refs.auditForm.validate((valid) => {
        if (valid) {
          const auditFormObj = this.auditForm
          this.$emit('auditForm', auditFormObj)
        }
      })
    },
    cancle(auditForm) {
      this.$refs.auditForm.resetFields()
      const close = false
      this.$emit('cancle', close)
    }
  },
  created() {
  }
}
</script>
