<template>
  <ContentCard class="content">
    <ContentWrapper
      :filter="filter"
      :filterSchema="filterSchema"
      :pagination="pagination"
      @filter-change="handleFilterChange"
      @filter-reset="handleFilterReset"
    >
      <ButtonGroupForListPage
        pageName="cornerIcon"
        @add="handleCreate"
        @edit="handleEdit"
        @delete="handleDelete"
      ></ButtonGroupForListPage>
      <!-- <div class="btns">
        <el-button type="primary" icon="el-icon-plus" @click="addData"></el-button>
        <el-button type="primary" icon="el-icon-edit" @click="editData"></el-button>
        <el-button type="primary" icon="el-icon-delete" @click="deleteData"></el-button>
        <el-button type="primary" icon="el-icon-edit-outline" @click="batchHandle"></el-button>
        <el-button type="primary" @click="changePriority"></el-button>
      </div>-->
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        :select-on-row-click="true"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"
      />
    </ContentWrapper>
    <!--  -->
    <el-dialog title="" :visible.sync="picDialogVisible" width="30%">
      <span class="pics">
        <img :src="reviewPicUrl" alt="" style="width:200px">
      </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="dialogPLVisible" :before-close="handleDialogClose">
      <GlobalIconAudit @auditForm="submitForm" @cancle="cancle" ref="auditForm"></GlobalIconAudit>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="dialogLevelVisible">
      <!-- <GlobelIconLevel @level="handleLevel"></GlobelIconLevel> -->
      <GlobelIconLevel></GlobelIconLevel>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogLevelVisible = false"> </el-button>
        <el-button type="primary" @click="dialogLevelVisible = false"> </el-button>
      </span>
    </el-dialog>
  </ContentCard>
</template>
<script>
import _ from 'gateschema'
import ButtonList from './../../components/ButtonLIst'
import GlobalIconAudit from './GlobalIconAudit'
import GlobelIconLevel from './GlobelIconLevel'
import ButtonGroupForListPage from '@/components/ButtonGroupForListPage'

import { ContentWrapper, Table, ActionList, utils } from 'admin-toolkit'
import BaseList from '@/components/BaseList'
export default {
  extends: BaseList,
  components: {
    ActionList,
    Table,
    ContentWrapper,
    GlobalIconAudit,
    GlobelIconLevel,
    ButtonGroupForListPage
  },
  data() {
    return {
      resourceType: 'cornerIcon',
      checkAll: false,
      globalTypes: {
        test1: '1010',
        test2: '1011',
        : '101',
        : '103',
        : '105',
        : '104',
        : '102'
      }, // 
      attributeTypes: {}, // 
      dialogPLVisible: false,
      dialogLevelVisible: false,
      typePositions: {
        : 0,
        : 1,
        : 3,
        : 2
      },
      picDialogVisible: false, // 
      reviewPicUrl: null,
      filter: this.genDefaultFilter(),
      filterSchema: null,
      pagination: {},
      selected: [],
      table: {
        props: {},
        header: [
          {
            label: 'ID',
            prop: 'cornerIconId'
          },
          {
            label: '',
            prop: 'cornerIconName',
            render: (createElement, { row }) => {
              return createElement(
                'el-button',
                {
                  attrs: {
                    type: 'text'
                  },
                  on: {
                    click: (event) => {
                      event.stopPropagation()
                      this.handleRead(row)
                    }
                  }
                },
                row.cornerIconName
              )
            }
          },
          {
            label: '',
            prop: 'imgUrl',
            render: (createElement, { row }) => {
              return createElement('img', {
                attrs: {
                  src: row.imgUrl,
                  width: '50px',
                  height: '50px',
                  class: 'imgs'
                },
                on: {
                  click: (event) => {
                    event.stopPropagation()
                    this.reviewPic(row)
                  }
                }
              })
            }
          },
          {
            label: '',
            prop: 'cornerStatus',
            render: (createElement, { row }) => {
              return this.$consts.statusText[row.cornerStatus]
            }
          }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    }
  },
  methods: {
    genDefaultFilter() {
      return {
        idPrefix: this.$consts.idPrefix
      }
    },
    fetchData() {
      this.handleAllRowSelectionRemove()
      const filter = this.parseFilter()
      this.$service.getGlobalMgrList(filter).then(data => {
        console.log(data)
        this.pagination.total = data.total
        this.table.data = data.rows
      })
    },
    parseFilter() {
      const { filter, pagination } = this
      if (pagination) {
        filter.page = pagination.currentPage
        filter.rows = pagination.pageSize
      }
      return filter
    },
    // 
    batchHandle() {
      var that = this
      if (that.selected.length == 0) {
        that.$message('')
      } else {
        console.log(that.table.data)
        const ids = that.selected
        for (var i = 0; i < ids.length; i++) {
          for (var j = 0; j < that.table.data.length; j++) {
            if (ids[i] == that.table.data[j].cornerIconId) {
              if (that.table.data[j].cornerStatus == 3 || that.table.data[j].cornerStatuses == 2) {
                this.dialogPLVisible = true
              } else {
                that.$message('[' + ids[i] + ']' + '')
              }
            }
          }
        }
      }
    },
    submitForm(data) {
      const auditForm = data
      this.$service
        .batchAudit(
          {
            idStr: this.selected.join(','),
            auditFlag: auditForm.auditFlag,
            auditDesc: auditForm.auditDesc
          },
          ''
        )
        .then(data => {
          this.fetchData()
          this.$refs.auditForm.cancle()
          this.dialogPLVisible = false
        })
    },
    // 
    cancle(data) {
      this.dialogPLVisible = data
    },
    handleDialogClose() {
      this.$refs.auditForm.cancle()
    },
    changePriority() {
      this.dialogLevelVisible = true
    },
    // 
    handleFilterChange(type, filter) {
      if (filter) { this.filter = filter }
      if (this.$validateId(this.filter.cornerIconId)) {
        if (type === 'query') {
          if (this.pagination) {
            this.pagination.currentPage = 1
          }
        }
        this.fetchData()
      }
    },
    // 
    handleFilterReset() {
      this.filter = this.genDefaultFilter()
      this.pagination.currentPage = 1
      this.fetchData()
    },
    // 
    getCornerTypes() {
      return this.$service.getCornerTypes().then(data => {
        data.forEach(element => {
          this.globalTypes[element.typeName] = element.typeId
        })
        console.log(this.globalTypes)
      })
    },
    // 
    getIconAttributes() {
      return this.$service.getIconAttributes().then(data => {
        data.attributes.forEach(element => {
          this.attributeTypes[element.attributeName] = element.attributeCode
        })
        console.log(this.attributeTypes)
      })
    },
    reviewPic(row) {
      this.reviewPicUrl = row.imgUrl
      this.picDialogVisible = true
    }
  },
  created() {
    let filterSchema = _.map({
      cornerIconName: _.o.string.other('form', {
        component: 'Input',
        placeholder: '  '
      }),
      cornerIconId: _.o.string.other('form', {
        component: 'Input',
        placeholder: 'ID'
      }),
      typeId: _.o.enum(this.globalTypes).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      typePosition: _.o.enum(this.typePositions).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      attributeCode: _.o.enum(this.attributeTypes).other('form', {
        component: 'Select',
        placeholder: ''
      }),
      cornerStatus: _.o.enum(this.$consts.statusEnums).other('form', {
        component: 'Select',
        placeholder: ''
      })
    }).other('form', {
      cols: {
        item: 6,
        label: 0,
        wrapper: 20
      },
      layout: 'inline',
      footer: {
        cols: {
          label: 0,
          wrapper: 24
        },
        showSubmit: true,
        submitText: '',
        showReset: true,
        resetText: ''
      }
    })

    if (this.$consts.idPrefix != '10') {
      filterSchema.map({
        idPrefix: _.o.enum({
          '': '10',
          '': '11'
        }).other('form', {
          component: 'Select',
          placeholder: ''
        })
      })
    }
    this.fetchData()
    this.getCornerTypes().then(() => {
      this.getIconAttributes().then(() => {
        this.filterSchema = filterSchema
      })
    }) // 

    // 
  }
}
</script>

<style lang = 'stylus' scoped>
.btns
  margin-bottom: 10px
</style>
